@import url("https://fonts.googleapis.com/css2?family=Noto+Kufi+Arabic:wght@100;200;300;400;500&family=Roboto:wght@300&family=Source+Code+Pro:ital,wght@1,600&display=swap");
body {
  font-family: "Noto Kufi Arabic", sans-serif;
  background-color: #ebebeb;
}
body * {
  color: #3c3c3c;
  text-align: right;
  justify-content: space-around;
}

.bg-gray {
  background-color: #ebebeb !important;
}

.border-left-1 {
  border-top: 0 !important;
  border-bottom: 0 !important;
  border-right: 0 !important;
}

.border-right-1 {
  border-top: 0 !important;
  border-bottom: 0 !important;
  border-left: 0 !important;
}

.border-top-1 {
  border-left: 0 !important;
  border-right: 0 !important;
  border-bottom: 0 !important;
}

.border-bottom-1 {
  border-left: 0 !important;
  border-right: 0 !important;
  border-top: 0 !important;
}

.gap-0-5 {
  gap: 0.5rem;
}

.bg-gray {
  background-color: #ebebeb;
  color: #666565;
  font-weight: 700;
}

.bg-black {
  background-color: black !important;
  color: white !important;
  font-size: 1.3rem !important;
}

.section-design {
  padding: 0.5rem 1.8rem 0;
  margin-bottom: 1rem;
}

::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  margin-top: 0.2rem;
  border-radius: 50px;
  background-color: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.sm-border {
  position: absolute;
  top: 10%;
  width: 1.4px;
  height: 0.8rem;
  background-color: rgba(201, 201, 201, 0.6);
}

.carousel-indicators li {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
}

.carousel-caption {
  position: absolute;
  height: 102px;
  width: 100%;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
  height: fit-content;
}
.carousel-caption p {
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
  padding: 0 0 0.8rem;
}

.news-paper_title {
  width: fit-content;
  background-color: #ebebeb;
  color: #cf2828;
  min-width: 4.5rem;
  border-right: 0.3rem solid #cf2828 !important;
  border-left: 0.3rem solid transparent;
  font-size: 0.9rem;
  font-weight: 600;
  text-align: center;
}

.card {
  border: none;
}

.card-title {
  font-size: 0.965rem;
  font-weight: 700;
  color: #666565;
}

.card-header {
  background-color: #afaeae;
  font-weight: 600 !important;
  font-size: 1.1rem;
  color: #424141;
  min-height: 3.8rem;
}

.text-black {
  color: black !important;
}

.play {
  position: absolute;
  z-index: 1;
  left: 1rem;
  top: 10px;
  border: 3px solid white;
  border-radius: 10%;
  display: flex;
}
.play i {
  font-size: 14px;
  color: #fffffb;
}
.play-small {
  padding: 0.1rem 0.3rem;
  border-width: 2.5px;
}
.play-small i {
  font-size: 7px;
}
.play-mid {
  padding: 0.4rem 0.8rem;
}
.play-mid i {
  font-size: 12px;
}

.pictures {
  position: absolute;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  top: 1.5rem;
}

.active {
  background-color: rgba(112, 115, 122, 0.1) !important;
}
.active .news-paper_title {
  background-color: #01655b;
  color: white !important;
  border-color: #01655b !important;
}

.btn {
  background-color: white;
  border-radius: 10%/50%;
  color: #666565;
  margin-left: -1rem;
  margin-bottom: -0.2rem;
  margin-right: 2rem;
}

.modal-dialog .close {
  position: absolute;
  top: -1rem;
  right: 5rem;
  color: red;
  width: 2rem;
  height: 2rem;
  font-size: 2rem;
  display: flex;
  align-items: center;
  text-align: center;
  border: 2px solid black;
  border-radius: 50%;
  background-color: white;
  outline: none;
  opacity: 1;
}
.modal-body {
  padding: 0;
}
.modal-body .footer {
  height: fit-content;
}
.jumbotron {
  height: 6.7rem;
}
.jumbotron ul {
  width: 25rem;
}
.jumbotron ul .sm-border {
  width: 2px;
  height: 0.9rem;
}
.jumbotron .nav-link {
  padding: 0;
  text-transform: capitalize;
}
.jumbotron .lang {
  font-size: 1rem;
  text-transform: uppercase;
}
.jumbotron .nav-item {
  padding: 0;
  position: relative;
}
.jumbotron .nav-item a {
  padding: 0;
  padding-right: 0.7rem;
  font-size: 11.5px;
  font-weight: 600;
  text-transform: uppercase;
}
.jumbotron .btn {
  border: 1px solid #c9c9c9 !important;
  border-radius: 10%/50% !important;
  margin-bottom: -2px;
  margin-right: 3px;
}
.jumbotron .btn a {
  margin: 1px 0;
  padding: 0 0.7rem;
}
.jumbotron .btn a i {
  margin-left: 2px;
  color: #848388;
}

.nav-section {
  height: 11.1rem;
}
.nav-section .active {
  background-color: transparent !important;
}
.nav-section * {
  color: #b40031;
  font-weight: 600;
}
.nav-section .navbar-toggler i {
  font-size: 1.8rem;
}
.nav-section i {
  font-size: 1.4rem;
}
.nav-section .nav-item {
  padding: 0;
}
.nav-section .nav-item a {
  padding: 0;
  margin: 0;
}
.nav-section .search {
  position: relative;
}
.nav-section .search .sm-border {
  height: 40px;
  left: 50px;
  top: -10px;
}

.active {
  color: #70737a !important;
}

