}
.news-first-section .card-header .navbar-news-carousel .carsoul-inner {
  margin-right: 1rem;
  padding: 0;
}
.news-first-section .card-header .navbar-news-carousel .carsoul-inner * {
  overflow: hidden;
  display: -webkit-box !important;
  -webkit-line-clamp: 1;
