.news-fifth-section .second img {
  width: 100%;
  height: 100%;
}

.arrow-up {
  height: 16.1vh;
  display: flex;
  align-items: center;
}
.arrow-up .btn {
  background-color: rgba(255, 255, 255, 0.6);
  border: 2px solid rgba(128, 128, 128, 0.8);
  font-size: 20px;
  font-weight: 100;
}
.arrow-up .btn i {
  padding: 7px 4px;
  color: #3c3c3c;
}

.footer {
  border-top: 3px solid rgba(175, 174, 174, 0.5);
  height: 85vh;
}
.footer .active {
  background-color: transparent !important;
}
.footer * {
  color: rgba(60, 60, 60, 0.8);
}
.footer-element .col-4 {
  display: flex;
  padding: 0;
  justify-content: flex-start;
}
.footer img {
  width: 15rem;
}
.footer .list-group {
  padding: 0;
}
.footer .list-group-item {
  font-size: 0.9rem;
  font-weight: 700;
  border: none;
  padding: 0;
}
.footer .list-group-item a {
  color: black;
}
.footer-contact {
  font-weight: 600;
  font-size: 0.8rem;
}
.footer-contact a {
  position: relative;
}
.footer-contact .sm-border {
  left: 0;
  top: 35%;
}

.list-group {
  display: inline-block;
}

